import { Component, OnInit } from '@angular/core';
import {ModalController} from '@ionic/angular';
import {ServicesModalComponent} from '../components/services-modal/services-modal.component';
import {UsersModalComponent} from '../components/users-modal/users-modal.component';
import {CommunicationService} from '../communication.service';
import {ApiService} from '../../../api.service';
import * as moment from 'moment';

@Component({
  selector: 'app-users',
  templateUrl: './users.page.html',
  styleUrls: ['./users.page.scss'],
})
export class UsersPage implements OnInit {
  usersList: any = [];
  selectedUser = null;
  constructor(private adminService: ApiService, private communicationService: CommunicationService, public modalController: ModalController) { }

  async presentModal(userData) {
    const modal = await this.modalController.create({
      component: UsersModalComponent,
      cssClass: 'admin-modal-class',
      backdropDismiss: true,
      swipeToClose: true,
      showBackdrop: true,
      componentProps: userData
    });
    return await modal.present();
  }

  onEdit(userData) {
    this.selectedUser = userData;
    this.presentModal(userData);
  }

  ionViewWillEnter() {
    this.communicationService.pageTitle.next('Users');
  }

  ionViewWillLeave() {
    this.closeModal();
  }

  async closeModal() {
    const onClosedData: string = 'Wrapped Up!';
    await this.modalController.dismiss(onClosedData);
  }

  ngOnInit() {
    this.communicationService.pageTitle.next('Users');
    this.getAllUserDetails();
  }


  getAllUserDetails() {
    this.adminService.getAllUserDetails().subscribe(
        res => this.getUserDetailsSuccess(res),
        error => {
          this.adminService.commonError(error);
        }
    );
  }

  getUserDetailsSuccess(res) {
    this.usersList = res;
    this.usersList = this.usersList.map((user) => {
      user.dateOfBirth = moment(new Date(user.dateOfBirth)).format('L');
      user.age = moment().diff(user.dateOfBirth, 'years',false);
      return user;
    });
  }

  onDelete(userData) {
    console.log(userData);
  }
}
