import { Component, OnInit } from '@angular/core';
import {ModalController, NavParams} from '@ionic/angular';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {SharedServiceService} from '../../../../shared-service.service';
import {ApiService} from '../../../../api.service';

@Component({
  selector: 'app-users-modal',
  templateUrl: './users-modal.component.html',
  styleUrls: ['./users-modal.component.scss'],
})
export class UsersModalComponent implements OnInit {
  userForm: FormGroup;
  editMode = false;
  imageBase64: any = '';
  imageUrl: any = '';
  userId: any = '';
  selectedImage: any = null;
  constructor(private navParams: NavParams, private adminService: ApiService, private sharedService: SharedServiceService, private formBuilder: FormBuilder, private modalController: ModalController) { }

  onSubmit() {
    if (this.userForm.invalid) {
      this.sharedService.presentToast('Please fill all the mandatory fields', 'error');
      return;
    }
    if (this.editMode) {
      this.patchUser();
    } else {
      this.createUser();
    }
  }


  patchUserData() {
    this.editMode = false;
    if (this.navParams && this.navParams.data && this.navParams.data.id) {
      this.editMode = true;
      this.patchModalData(this.navParams.data);
    }
  }

  patchModalData(patchData) {
    this.userId = patchData.id;
    this.userForm.get('name').setValue(patchData.name);
    this.userForm.get('mobileNo').setValue(patchData.mobileNo);
    this.userForm.get('isWhatsappAvailable').setValue(patchData.isWhatsappAvailable);
    this.userForm.get('role').setValue(patchData.role);
    this.userForm.get('isActive').setValue(patchData.subService.isActive);
    this.userForm.get('email').setValue(patchData.email);
    this.userForm.get('dob').setValue(patchData.dob);
    this.userForm.get('file').setValue(patchData.file);
    // This logic to update the image
    this.imageUrl = `${patchData.imageUrl}?${new Date().getTime()}`;
  }

  patchUser() {
    const formData = new FormData();
    formData.append('name', this.userForm.get('name').value);
    formData.append('mobileNo', this.userForm.get('mobileNo').value);
    formData.append('isWhatsappAvailable', this.userForm.get('isWhatsappAvailable').value);
    formData.append('role', this.userForm.get('role').value);
    formData.append('isActive', this.userForm.get('isActive').value);
    formData.append('email', this.userForm.get('email').value);
    formData.append('dob', this.userForm.get('dob').value);
    formData.append('file', this.userForm.get('file').value);
    if (this.imageBase64) {
      const fileName = `service-${(this.userForm.get('name').value).toLowerCase().trim().split(' ').join('-')}.${this.selectedImage.name.split('.')[1]}`;
      formData.append('file', this.selectedImage, fileName);
    }
    this.adminService.updateUser(formData, this.userId).subscribe(
        res => this.updateUserSuccess(res),
        error => {
          this.adminService.commonError(error);
        }
    );
  }

  createUser() {
    const formData = new FormData();
    const fileName = `user-${(this.userForm.get('name').value).toLowerCase().trim().split(' ').join('-')}.${this.selectedImage.name.split('.')[1]}`;
    formData.append('name', this.userForm.get('name').value);
    formData.append('mobileNo', this.userForm.get('mobileNo').value);
    formData.append('isWhatsappAvailable', this.userForm.get('isWhatsappAvailable').value);
    formData.append('role', this.userForm.get('role').value);
    formData.append('isActive', this.userForm.get('isActive').value);
    formData.append('email', this.userForm.get('email').value);
    formData.append('dob', this.userForm.get('dob').value);
    formData.append('file', this.selectedImage, fileName);
    this.adminService.createUser(formData).subscribe(
        res => this.createUserSuccess(res),
        error => {
          this.adminService.commonError(error);
        }
    );
  }

  createUserSuccess(response) {
    console.log(response);
  }

  updateUserSuccess(res) {
    this.closeModal();
  }

  ionViewWillLeave() {
    this.editMode = true;
  }

  ionViewWillEnter() {
    this.patchUserData();
  }

  ngOnInit() {
    this.userForm = this.formBuilder.group({
      name: new FormControl({value: '', disabled: false}, [
        Validators.required
      ]),
      mobileNo: new FormControl({value: '', disabled: false}, [
        Validators.required
      ]),
      isWhatsappAvailable: new FormControl({value: false, disabled: false}, [
        Validators.required
      ]),
      role: new FormControl({value: '', disabled: false}, [
        Validators.required
      ]),
      isActive: new FormControl({value: true, disabled: false}, [
        Validators.required
      ]),
      email: new FormControl({value: '', disabled: false}, [
        Validators.required
      ]),
      dob: new FormControl({value: '', disabled: false}, [
        Validators.required
      ]),
    });
  }

  async closeModal() {
    const onClosedData: string = "Wrapped Up!";
    await this.modalController.dismiss(onClosedData);
  }

}
